import React, {useState, useEffect} from 'react';
import {Link, withRouter} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Navbar, Nav } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import Routes from './Routes';
import axios from 'axios'
import './App.css';
import getUser from './services/getUser'
/* 
import Account from './components/Account';

import accountService from './services/accounts'
import loginService from './services/login' */

const axiosInstance = axios.create();

function App(props) {
  const [isAuthenticating, setIsAuthenticating] = useState(true);
  const [isAuthenticated, setUserIsAuthenticated] = useState(false);
  const [user, setUser] = useState(null);

  
  function handleLogout() {
    window.localStorage.removeItem('loggedAppUser')
    axiosInstance.defaults.headers.common['Authorization'] = null;
    setUserIsAuthenticated(false);
    props.history.push("/login")
  }

  // Run when first loading the app
  useEffect(() => 
  {
    onLoad()
  }, []);

  // check whether the user has previously logged in without logging out
  async function onLoad() 
  {
    console.log('onLoad')
    const loggedUserJSON = window.localStorage.getItem('loggedAppUser')
    if (loggedUserJSON) 
    {
      const user = JSON.parse(loggedUserJSON)
      setUser(user)
      const token = user.token
      console.log('user in onLoad', user)
      // check the authorization token 
      try 
      {
        axiosInstance.defaults.headers.common['Authorization'] = token;
        //const responseStatus = await authenticateSession({axiosInstance});
        const serverId = JSON.parse(loggedUserJSON).id
        console.log('serverId in onLoad', serverId)
        const userFromServer = await getUser({axiosInstance, serverId})
        console.log('auth response:', userFromServer)
        if (userFromServer) 
        {
            setUser(userFromServer)
            setUserIsAuthenticated(true)
        }
        else 
        {
          axiosInstance.defaults.headers.common['Authorization'] = null;
          window.localStorage.removeItem('loggedAppUser')
          setUserIsAuthenticated(false)
          console.log('auth fail')
        }
      }
      catch(exception) 
      {
        alert(exception)
      }

    }
    setIsAuthenticating(false)
  }

  return (
    !isAuthenticating &&
    <div className="App container">
       <Navbar fluid="true" collapseOnSelect bg="light" expand="lg">
          <Navbar.Brand as={Link} to="/">
            Säästötavoite
          </Navbar.Brand> 
          <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
       <Navbar.Collapse>
         <Nav className="ml-auto">
           {isAuthenticated
            ? <>
                <LinkContainer to="/account" >
                  <Nav.Link>Lisää pankkitili</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/goal" >
                  <Nav.Link>Aseta tavoite</Nav.Link>
                </LinkContainer>
                <Nav.Link onClick={handleLogout}>Kirjaudu ulos</Nav.Link>
              </> 
            : <>
                <LinkContainer to="/signup" >
                  <Nav.Link>Rekisteröityminen</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/login">
                  <Nav.Link>Sisäänkirjautuminen</Nav.Link>
                </LinkContainer>
              </>
           }
        </Nav>
       </Navbar.Collapse>
      </Navbar>
      <Routes appProps={{ isAuthenticated, 
                          setUserIsAuthenticated, 
                          user, 
                          setUser,
                          axiosInstance 
                        }} /> 
    </div>
  );
}

export default withRouter(App);
