
const loadAccounts = (axiosInstance, serverId) => {
    console.log('loadAccounts serverId', serverId)
    //console.log('axiosInstance')
    const baseUrl = `/api/accounts/${serverId}`
    const request = axiosInstance.get(baseUrl)
    return request.then((response) => {
        console.log(response)
        return response.data
    } )
}

export default loadAccounts