import React, {useState, useEffect} from 'react'
import './Home.css'
import loadAccounts from '../services/accounts'
import Account from './Account'
import {Table} from 'react-bootstrap'


export default function Home (props) {
  const [isLoading, setIsLoading] = useState(true);
  const [accounts, setAccounts] = useState([]);

  useEffect(() => {
    async function onLoad() {

      if(!props.isAuthenticated) 
      {
        return;
      }
      try 
      {
        const accounts = await getAccounts( props.axiosInstance, 
                                            props.user.id)
        setAccounts(accounts)
        console.log('accounts in home', accounts)
      }
      catch(e)
      {
        alert(e)
      }
      setIsLoading(false)
    }
    onLoad()
  }, [props.isAuthenticated, props.axiosInstance])

  const getAccounts = (axiosInstance, serverId) => {
    console.log('serverId in getAccounts', serverId)
    return loadAccounts(axiosInstance, serverId)
  }

  const renderAccountSum = () => {
    const sum = Array.from(accounts).reduce((acc, current) => acc + 
                                        parseFloat(
                                          current
                                          .balances[0]
                                          .balanceAmount
                                          .amount
                                        ), 0
    )  
    const difference = () => {
      const sumMinusGoal = sum-props.user.goal
      if (sumMinusGoal === 0) 
      {
        return (<h5>Tavoite saavutettu!</h5>)
      } 
      else if (sumMinusGoal > 0)
      {
        return (<h5>Tavoite ylitetty <span 
                                      className="tabOver">
                                        {sumMinusGoal} €
                                      </span></h5>)
      } 
      else
      {
        return (<h5>Tavoitteeseen jäljellä <span 
                                            className="tabUnder">
                                              {Math.abs(sumMinusGoal)} €
                                            </span></h5>)
      }

    }

    return(<div>
            <h5>Tileillä yhteensä <span className="tabAll">{sum} €</span></h5>
            {props.user.goal ? difference() : null}

          </div>)
  }

  function renderGoal() {
    return (
      <div className='goal'>
        <h5>Tavoite <span className="tabGoal">
          {props.user.goal} €
          </span></h5>
      </div>
    );
  }

  const noGoal = () => {
    return (
      <div className='goal'>
        <h5><b>Säästötavoitetta ei ole asetettu.</b></h5>
      </div>
    )
  }

  const renderAccounts = () => {
    return(
      <>
      <hr></hr>
      <h3>Tilit</h3>
      <Table  striped 
              bordered 
              hover
              responsive 
              className="individualAccounts">
        <thead>
          <tr>
            <th>IBAN</th>
            <th>Nimi</th>
            <th>Kirjatut varat</th>
          </tr>
        </thead>
        <tbody>{Array.from(accounts).map((account) => 
          {
            return (<Account key={account.iban} account={account} />
            )
          }
          )
        }
        </tbody>
      </Table>
      </>
    )
  }



  function renderLander() {
    return(
      <div className='lander'>
        <h1>Säästötavoite</h1>
        <p>Tarkista paljon säästettävää sinulla on 
           tavoitteeseesi. </p>
      </div>
    )
  }

  function renderGoalAndAccounts() {
    return(
      <div className='accounts'>
        <hr />
        <div>
          {isLoading && <h5>Ladataan tietoja...</h5>}
          {!isLoading && (props.user.goal ? renderGoal() : noGoal())}
          {!isLoading && renderAccountSum()}
          {!isLoading ? renderAccounts() : null} 
        </div>
      </div>
    )
  }

  return (
    <div className='Home'>
      {props.isAuthenticated ? renderGoalAndAccounts() : renderLander()}
    </div>  
    
  );
}
