import React, { useState } from "react";
import { FormGroup, FormControl } from 'react-bootstrap';
import LoaderButton from '../components/LoaderButton';
import "./Goal.css";
import setGoal from "../services/setGoal"
//import { useFormFields } from '../libs/hooksLib'

const Goal = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [fieldGoal, changeGoal] = useState("")

    async function handleSubmit(event) {
        event.preventDefault();
        console.log('goal user:', props.user)
        console.log('goal field', fieldGoal )
        setIsLoading(true);
        try 
        {
            const newUser = await setGoal(
                {goal:fieldGoal}, 
                props.user.id,
                props.axiosInstance)
            if(!newUser) 
            {
              props.setUserIsAuthenticated(false)
              setIsLoading(false)
              props.history.push('/')
            } 
            else
            { 
            props.setUser(newUser)
            setIsLoading(false)
            props.history.push('/')
            }
        }
        catch(exception)
        { 
            alert(exception.message)
            setIsLoading(false)
        }
      }
    return (
        props.isAuthenticated &&
        <div className="SetGoal">
        <form onSubmit={handleSubmit}>
        <FormGroup controlId="name" size="mg">
          <FormControl
            autoFocus
            placeholder="Uusi tavoite"
            type="number"
            value={fieldGoal}
            onChange={ e=> changeGoal(e.target.value)}
          />
        </FormGroup>
        <LoaderButton
          block
          type="submit"
          size="mg"
          isLoading={isLoading}
        >
          Aseta tavoite
        </LoaderButton>
      </form>

        </div>

    )
}

export default Goal